export default {
	data() {
		return {
			historyList:[],
			historyInfo: {},
			page:1,
			contentText: {
				contentdown: '查看更多',
				contentrefresh: '加载中',
				contentnomore: '- 暂时没有新内容了呢 -'
			},
			status: 'loading',
			code:'',
		};
	},
	onPullDownRefresh() {
		this.page = 1;
		this.historyInfo = {};
		this.historyList = []
		uni.showLoading({
			title: '加载中'
		});
		this.getList()
		uni.hideLoading();
		uni.stopPullDownRefresh()
	},
	onReachBottom(){
		if (this.code != '-116') {
			this.page = this.page + 1;
			this.getList();
		}
	},
	onShow(){
		this.page = 1;
		this.historyInfo = {};
		this.historyList = []
		this.getList()
	},
	methods:{
		getList(){
			if(uni.getStorageSync('userInfo')){
				let memberId = JSON.parse(uni.getStorageSync('userInfo')).id
				this.$Request.get(`/appScoreController.do?getScoreHistory&memberId=${memberId}&page=${this.page}`)
				.then(res => {
					this.code = res.code
					this.historyInfo = res.data
					this.status = 'noMore'
					if(res.code == 0){
						this.historyList =  [...this.historyList, ...res.data.historyList].map(item => {
							return {
								...item,
								createDate: item.createDate && item.createDate.substring(0,10)
							}
						})
					}else if(res.code == '-118' || res.code == '-116'){
						this.status = 'noMore'
					}else{
						uni.showToast({
							title: res.info,
							icon: 'none'
						})
					}
				})
			}else{
				this.status = 'noMore'
				uni.showModal({
				    title: '提示',
				    content: '您尚未登录，是否去登录？',
				    success: function (res) {
				        if (res.confirm) {
				            uni.navigateTo({
				            	url:'/pages/loginAll/login'
				            })
				        } else if (res.cancel) {
				            console.log('用户点击取消');
				        }
				    }
				});
			}
		},
	}
}
